import VerticalMenu from './VerticalMenu';
import React, {Component} from 'react'
import '../stylesheet_QueueIncidents.css';
import { Form, Segment, Image, Button } from 'semantic-ui-react';

class Profile extends Component{

    render(){
        return(
            <div>
                <div className="VerticalMenu">
                    <VerticalMenu/>
                </div>
                
                <div className="Profilepage">
                    {/* <h1>MY PROFILE BITCH!</h1> */}
                    <Segment.Group>
                        <Segment style={{color:'whitesmoke', fontSize:'30px'}}><b>PROFILE</b></Segment>
                        <Segment style={{paddingBottom:'75px'}}>
                            <div style={{color:'whitesmoke', fontSize:'16px'}}>
                                <p><b>Name: </b></p>
                                <p><b>Email: </b></p>
                                <p><b>Contact Number: </b></p>
                            </div>
                        </Segment>
                        <Segment style={{paddingBTop:'20px'}}>
                            <Button color='red' floated='right'>Update Information</Button>
                        </Segment>
                    </Segment.Group>
                </div>
            </div>
        )
    }



}

export default Profile;